@use 'base/variables' as v;
@use 'base/mixins' as m;



.modal {
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;

    .eliminar {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: transform;
        transition-duration: 500ms;
        border-radius: 3rem;

        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar{
            transform: translateY(-200%);
            opacity: .1;
        }

        legend{
            font-size: 2.8rem;
            color: v.$rojo;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        .opciones{
            display: flex;
            justify-content: space-between
        }

        .eliminar_usuario{
            @include m.boton(v.$rojo, v.$blanco);
        }
        .cerrar-modal{
            @include m.boton(v.$indigo, v.$blanco);
        }

        h2{
            color: v.$rojo;
        }
    }
}